<template>
  <div :class="tableClass">
    <b-scroll class="vbestui-table-scroll" :height="height">
      <table :class="mainTableClass" :style="mainTableStyle">
        <slot name="head"></slot>
        <slot name="body"></slot>
      </table>
    </b-scroll>
  </div>
</template>
<script>
const prefixCls = "vbestui-table";
export default {
  name: "BTable",
  provide() {
    return {
      vbestTable: this,
    };
  },
  data() {
    return {
      prefixCls: prefixCls,
    };
  },
  props: {
    height: {
      type: String,
      default: "",
    },
    /** */
    stripe: {
      type: Boolean,
      default: false,
    },
    stripeBackgroundColor: {
      type: String,
      default: "",
    },
    /** */
    split: {
      type: Boolean,
      default: false,
    },
    /** */
    splitColor: {
      type: String,
      default: "",
    },
    showBorder: {
      type: Boolean,
      default: false,
    },
    /**list */
    borderColor: {
      type: String,
      default: "",
    },
    warp:{
      type:Boolean,
      default:false
    }
  },
  computed: {
    tableClass() {
      return [`${prefixCls}`];
    },
    mainTableClass() {
      return [
        `${prefixCls + "-container"}`,
        this.split || this.showBorder ? `${this.prefixCls + "-border"}` : "",
      ];
    },
    mainTableStyle() {
      /** */
      return {
        borderColor: this.splitColor || this.borderColor,
      };
    },
  },
};
</script>